* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h3 {
  font-family: "Permanent Marker", cursive;
  letter-spacing: 0.25rem;
  color: #444;
  text-align: center;
}

form {
  padding: 25px;
}

input[type="text"],
.word-input,
.definition-input {
  text-align: center;
  margin: 10px;
  padding: 7.5px 25px;
  border: 0;
  border-radius: 0 !important;
  outline: none;
}

input[type="text"]:focus {
  outline: none;
}

input[type="submit"],
.submit {
  font-weight: bold;
  background: rgb(179, 179, 179) !important;
  margin: 10px auto;
  padding: 10px 100px;
  border: 0 !important;
  border-radius: 0 !important;
  display: block;
  cursor: pointer;
}

input[type="submit"]:hover {
  background: #999;
}

input[type="submit"]:focus {
  outline: none;
}

.App {
  background: darkturquoise;
  margin: 20px auto;
  padding: 50px;
  width: 75%;
  max-width: 700px;
  box-shadow: 0px 0px 25px darkslategray;
}

.navbar {
  padding: 15px 20px;
  text-align: center;
}

.navbar h1 {
  font-family: "Permanent Marker", cursive;
  font-size: 2.5rem;
  letter-spacing: 0.75rem;
  margin-right: -0.85rem;
  text-transform: uppercase;
  padding-bottom: 10px;
}

.accent-txt {
  font-size: 1.25rem;
  font-weight: bold;
  color: gold;
}

.vocab {
  word-wrap: break-word;
}

.word-list {
  margin: 20px;
}

.word-list ul {
  padding: 0;
  list-style-type: none;
}

.word-list li {
  color: aliceblue;
  background: black;
  margin: 25px;
  padding: 25px;
  text-align: center;
  cursor: pointer;
}

.word-list:hover {
  opacity: 0.85;
}

.word-list .word {
  font-size: 1.75rem;
  font-weight: bold;
  text-align: center;
  padding-bottom: 25px;
}

.word-list .word:hover {
  opacity: 0.75;
  text-decoration: line-through;
}

.word-list .definition {
  color: #999;
}

.word,
.word-of-the-day {
  text-transform: uppercase;
}

.input {
  display: flex;
  justify-content: center;
}

.empty {
  margin: 20px;
  text-align: center;
}

.words-of-the-day {
  background: rgb(240, 240, 240);
  padding: 55px 22.5px;
  width: 25vw;
  height: 100vh;
  top: 0;
  left: 0;
  box-shadow: 0px 0px 20px darkslategray;
  position: fixed;
  z-index: 99;
}

.word-of-the-day {
  font-size: 1.25rem;
  font-weight: bold;
  padding: 35px 5px 10px 5px;
}

.add-btn {
  font-family: "Permanent Marker", cursive;
  font-size: 2.5rem;
  color: orange;
  text-align: center;
  cursor: pointer;
}

.add-btn:hover {
  color: darkorange;
}

.close-btn {
  font-size: 1.75rem;
  top: 75%;
  position: absolute;
  cursor: pointer;
}

.close-btn-active {
  transform: translateX(-150%);
  transition: all 0.8s ease-in-out;
}

.footer {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 0.8rem;
  letter-spacing: 0.75px;
  margin-right: -0.75px;
  text-align: center;
  bottom: 0;
  width: 100;
  margin: 35px;
}

@media only screen and (max-width: 680px) {
  input[type="text"] {
    margin: 0px;
    padding: 7.5px 5px;
    display: block;
  }

  input[type="submit"] {
    font-weight: bold;
    background: rgb(179, 179, 179);
    margin: 10px auto;
    padding: 10px 35px;
  }

  .App {
    padding: 45px 5px;
    margin: 0px auto;
    text-align: center;
  }

  .navbar {
    padding: 5px;
  }

  .navbar h1 {
    font-size: 1.25rem;
    padding-bottom: 25px;
  }

  .navbar p,
  .empty {
    font-size: 0.95rem;
  }

  .accent-txt {
    font-size: 1.25rem;
  }

  .input {
    margin: 0px 0px 25px 0px;
    display: flex;
    flex-direction: column;
  }

  .word-list li {
    padding: 25px 15px;
    margin: 2.5px;
  }

  .word-list .word {
    font-size: 1.5rem;
  }

  .words-of-the-day {
    width: 100vw;
    opacity: 0.95;
  }

  .footer {
    font-size: 0.75rem;
  }
}
